@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navContainer {
  background-color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
}
.navUl {
  font-family: "Lobster", cursive;
  width: 70%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
li {
  color: black;
  font-size: 1.5rem;
}
.navUl > li:hover {
  scale: 1.1;
}
body {
  background-color: rgba(248, 253, 254, 0.409);
}
.link {
  color: white;
  text-decoration: none;
}
.centered {
  width: 100%;
  margin: 0 auto;
}
.logoR {
  font-size: 2.5rem;
  color: rgb(231, 183, 23);
  font-weight: bold;
}
.logoName {
  font-size: 1.7rem;
  color: black;
  font-weight: bold;
  letter-spacing: 3px;
}

.homeContainer {
  background-image: linear-gradient(
      to top,
      rgba(2, 0, 0, 0.842) 60%,
      rgba(62, 62, 63, 0.856)
    ),
    url(/react-router-restaurant/static/media/jason-leung-unsplash.25fe64fb.jpg);
  width: 100vw;
  height: 90vh;
  background-size: cover;
  padding-top: 5rem;
}
.homeGreets {
  width: 45%;
  height: 70%;
  margin: 0 35%;
  background-image: url(/react-router-restaurant/static/media/priscilla-du-preez-unsplash.dd1167cf.jpg);
  background-size: cover;
  padding-top: 3rem;
  -webkit-filter: opacity(0.7);
          filter: opacity(0.7);
  border: 1px solid rgb(231, 231, 231);
}

.homeTitle {
  background-color: black;
  color: rgb(211, 154, 50);
  padding-top: 2rem;
  padding-bottom: 2rem;
  letter-spacing: 3px;
  font-size: 2rem;
  text-align: center;
  text-transform: uppercase;
  font-family: "Fjalla One", sans-serif;
}
.homeTitleLine {
  display: block;
  margin: 0 auto;

  width: 200px;
  height: 1px;
  background-color: rgba(252, 252, 249, 0.74);
}
form {
  margin-top: 2rem;
}
.homeBooking {
  width: 350px;
  height: 90vh;
  background-color: rgba(0, 0, 0, 0.616);
  position: absolute;
  bottom: 0;
  left: 50px;

  outline-offset: 10px;
  border-left: 1px solid rgba(223, 215, 211, 0.507);
  border-right: 1px solid rgba(223, 215, 211, 0.507);
}
.bookName {
  width: 85%;
  height: 35px;
}
.bookEmail {
  width: 85%;
  height: 35px;
}
.bookPhone {
  width: 85%;
  height: 35px;
}

.bookbtn {
  width: 100%;
  height: 50px;
  font-size: 1.5rem;
  text-transform: uppercase;
  margin-top: 2rem;

  border-radius: 5px;
  color: white;
  background-color: rgb(211, 154, 50);
}
input {
  border-bottom: 3px transparent;
  text-align: center;
  min-height: 35px;
  margin: 0 auto;
  display: block;
  border-radius: 6px;
}
input:focus {
  outline: none;
  border-bottom: 3px solid brown;
  box-shadow: 0 1rem 2rem rgba(248, 233, 233, 0.233);
}
input:focus:invalid {
  border-bottom: 3px solid burlywood;
}
input::-webkit-input-placeholder {
  color: grey;
}
label {
  font-size: 1.2rem;
  color: rgb(217, 225, 233);

  font-weight: 500;
  margin-left: 2rem;
  margin-top: 0.7rem;
  margin-bottom: 0.7rem;
  display: block;
}
input:-ms-input-placeholder + label {
  opacity: 0;
}
input:placeholder-shown + label {
  opacity: 0;
}
.bookDate {
  border-bottom: 3px transparent;
  float: left;
  color: rgb(58, 58, 58);
  padding: 0;
}
.bookTime {
  border-bottom: 3px transparent;
  border-radius: 6px;
  float: right;

  color: rgb(26, 24, 24);
  padding: 0;
}
.bookBottom {
  max-width: 300px;
  height: 70px;
  margin: 0 auto;
}

.menuContainer {
  padding: 3rem;
  background-color: rgba(248, 253, 254, 0.409);
  display: grid;
  grid-gap: 20px;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
}

.menuTitle {
  padding-top: 2rem;
  background-color: rgba(248, 253, 254, 0.409);
  text-align: center;
  color: rgb(211, 154, 50);
  font-family: "Fjalla One", sans-serif;
  font-family: "Lobster", cursive;
  text-transform: capitalize;
}

.recipe {
  display: grid;
  grid-template-columns: 150px 1fr;
  grid-gap: 10px;
  gap: 10px;
  border-image-outset: 0;
  align-items: flex-start;
  padding: 20px;
}
.recipeDetails {
  height: 50%;
  display: grid;
  align-content: flex-end;
  grid-column-start: 2;
  grid-row-start: 1;
}
.recipe_title,
.recipe_type {
  text-align: center;

  color: black;
  font-weight: lighter;
}
.menuImage {
  border: 3px solid rgba(196, 135, 39, 0.836);

  border-radius: 50%;
  width: 10rem;
  height: 10rem;
}

.menuImg {
  -webkit-clip-path: circle(50% at 50%50%);
          clip-path: circle(50% at 50%50%);
  width: 10rem;
  height: 10rem;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transform: translate(-3px, -3px) scale(0.9);
}
.menuButton {
  color: white;
  background-color: black;
  text-transform: uppercase;
  width: 100px;
  height: 40px;
  border-radius: 5px;
}
.button_container {
  display: grid;
  align-self: flex-end;
  justify-content: space-around;
  grid-column-start: 2;
  grid-row-start: 1;
}

.popup {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 8000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
}
.linkMore {
  text-decoration: none;
  color: white;
}

.sidebarStyle {
  display: inline-block;
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  margin: 12px;
  background-color: #404040;
  color: #ffffff;
  z-index: 1 !important;
  padding: 6px;
  font-weight: bold;
}

.mapContainer {
  margin: 2rem;
  width: 100%;
  height: 100%;
  display: grid;
  align-self: center;
  justify-content: flex-start;
}

.address {
  font-family: "Abel", cursive;

  padding: 15px;
  width: 270px;
  height: 200px;

  margin-right: 10rem;
  margin-top: 5rem;
  align-self: flex-start;
  justify-self: flex-end;
  display: grid;

  letter-spacing: 2px;
  left: 1.5rem;
}

h3 {
  text-transform: uppercase;
  font-family: "Fjalla One", sans-serif;
  color: rgb(211, 154, 50);
}
.contact-container {
  height: 90vh;
  background-color: rgba(224, 244, 248, 0.409);
  display: grid;
  grid-gap: 4rem;
  gap: 4rem;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
}
.map {
  width: 100%;
  height: 70%;
}

.recipeContainer {
  background-color: rgb(255, 255, 255);
  width: 75%;
  position: fixed;
  box-shadow: 0 2rem 4rem rgba(0, 0, 0, 0.2);
  border-radius: 10px;

  top: 10%;
  left: 10%;
}
.recipeContent {
  padding: 2rem;

  overflow: hidden;

  transition: all 0.4s;
}
.recipeContainer:target {
  transform: translate(-50%, -50%) scale(1);
}
.recipeTitle {
  font-family: "Lobster", cursive;
  font-size: 1.5rem;
  text-align: center;

  margin-bottom: 2rem;
  margin-top: 1rem;
  color: rgb(231, 183, 23);
}
.closeRecipe {
  color: rgb(52, 52, 52);
  position: absolute;
  top: 1rem;
  right: 2.5rem;
  font-size: 2.3rem;
  text-decoration: none;
  display: inline-block;
  transition: all 0.2s;
  line-height: 1;
}
.recipeImg {
  max-width: 250px;

  margin: 7px 30px 10px 0;
  float: left;
}

.recipeSteps {
  float: right;
  margin: 0 0 7px 7px;
  line-height: 1.5;
  font-family: "Abel", cursive;
  font-size: 1.1rem;
}
span {
  width: 100%;
}

.recipeType {
  margin-top: 1rem;

  width: 50%;
}
.recipeTime {
  width: 50%;
}
.recipe-pin-icon {
  width: 200px;
  height: 100px;
  position: absolute;
  top: 15rem;
  left: 15rem;
  font-size: 3rem;

  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  z-index: 1000;
}
.pin-icon {
  width: 100%;
  height: 100%;
}
.recipe-btn {
  font-size: 1.8rem;
  display: block;
  float: right;
  color: white;
  background-color: black;
  margin-top: 1.1rem;
  font-family: "Fjalla One", sans-serif;
  width: 100px;
  height: 40px;
  border-radius: 5px;
}
.recipe-detail {
  width: 100%;
  display: block;
}
.recipe-detail-div {
  display: inline-block;
  width: 60%;
  margin-right: 5rem;
}

.orderContainer {
  display: flex;
  text-align: center;
  justify-content: center;
  flex-direction: column;
  border: 2px solid rgb(118, 118, 118);
  border-radius: 8px;
  border-left-style: inset;
  border-right-style: inset;
  width: 75%;
  margin: 0 auto;
  background-color: rgb(255, 255, 254);
}
.orderLi {
  list-style: none;
  height: 2.5rem;
  margin: 0.8rem;
  padding-top: 0.5rem;
}
.orderTitle {
  font-size: 1.7rem;
  font-weight: 600;
  font-family: "Abel", cursive;
  display: inline-block;
}
.emptyOrder {
  text-align: center;
  margin-top: 10rem;
}
input {
  background-color: rgb(255, 255, 254);
  width: 50%;
  height: 2.4rem;
}
.formName {
  text-align: center;
  font-family: "Lobster", cursive;
  font-size: 1.5rem;
  color: rgb(211, 154, 50);
  padding-top: 1rem;
}
.orderBtn {
  width: 50%;
  height: 50px;
  font-size: 1.5rem;
  text-transform: uppercase;
  margin-top: 2rem;

  border-radius: 5px;
  color: white;
  background-color: rgb(245, 162, 9);
}
label {
  padding-left: 3rem;
  font-size: 1.4rem;
  color: rgba(36, 32, 31, 0.911);
}
.orderForm {
  background-image: linear-gradient(
      to right,
      rgba(23, 39, 90, 0.157),
      rgba(226, 226, 226, 0.979)
    ),
    url(/react-router-restaurant/static/media/order.5820d719.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  margin-top: 0;
}
.completed {
  opacity: 0.5;
  text-decoration: line-through;
  transition: all 0.3s ease;
}
.deleteBtn {
  float: right;
  width: 1.8rem;
  color: white;
  border-radius: 5px;
  height: 2rem;
  margin-right: 4rem;
  background-color: rgb(33, 33, 35);
}

